export const LOGIN = 'LOGIN';
export const LOGOUT = 'LOGOUT';
export const ERROR = 'ERROR';

export const login = (tokenObject) => ({
    type: LOGIN,
    tokenObject
});

export const error = (error) => ({
    type: ERROR,
    error
});


